<template>
    <div class="waring">
        <hheader :header="hheader">
            <i class="iconfont icon-ren" slot="hleft"></i>
        </hheader>
        <section class="container has-header has-gray has-footer">
            <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                <van-list v-model="loading" :finished="finished" @load="onLoad">
                    <section class="selectList" :class="{'liston':list.length==0}">
                        <!-- <select name="等级" id="" v-model="degree" @change="init()">
                            <option value="">全部</option>
                            <option value="1">一级</option>
                            <option value="2">二级</option>
                            <option value="3">三级</option>
                        </select>
                        <select name="" id="" v-model="deviceType" @change="init()">
                            <option value="">设备</option>
                            <option value="1">DTU</option>
                            <option value="2">传感器</option>
                        </select>
                        <select name="" id="" v-model="dealStatus" @change="init()">
                            <option value="">状态</option>
                            <option value="1">已处理</option>
                            <option value="2">未处理</option>
                        </select> -->
                        <div @click="show=!show">{{degreeTXT}}</div>
                        <div @click="show1=!show1">{{deviceTypeTXT}}</div>
                        <div @click="show2=!show2">{{dealStatusTXT}}</div>
                        <ul class="selectul" v-show="show">
                            <li @click="degreeTXT='全部';dj()">全部</li>
                            <li @click="degreeTXT='一级';dj(1)">一级</li>
                            <li @click="degreeTXT='二级';dj(2)">二级</li>
                            <li @click="degreeTXT='三级';dj(3)">三级</li>
                        </ul>
                        <ul class="selectul" v-show="show1">
                            <li @click="deviceTypeTXT='全部';sb()">全部</li>
                            <li @click="deviceTypeTXT='DTU';sb(1)">DTU</li>
                            <li @click="deviceTypeTXT='传感器';sb(2)">传感器</li>
                        </ul>
                        <ul class="selectul" v-show="show2">
                            <li @click="dealStatusTXT='全部';zt()">全部</li>
                            <li @click="dealStatusTXT='已处理';zt(1)">已处理</li>
                            <li @click="dealStatusTXT='未处理';zt(2)">未处理</li>
                        </ul>
                    </section>
                    <!--  表  -->
                    <ul class="waring-item" v-for="(v,i) in list" @click="toDetail(v)" v-if="list.length>0" v-cloak>
                        <li>
                            <b>
                                {{v.device_name+" "+v.point_position}}
                            </b>
                            <span class="badge">{{v.degree}}</span>
                        </li>
                        <li>
                            原因:{{v.reason}}
                        </li>
                        <li>
                            次数:
                            <span class="TxtColor">{{v.times}}</span>
                        </li>
                        <li>
                            <i class="iconfont icon-shijian"></i>{{v.work_time}}
                            <span class="stateName" v-if="v.deal_status==1">已处理</span>
                            <span class="stateName error" v-else>未处理</span>
                        </li>
                    </ul>
                    <div class="hdefalut" v-show="list.length==0">
                        暂无数据
                    </div>
                </van-list>
            </van-pull-refresh>
        </section>
        <hfooter></hfooter>
        <!-- 弹出框 -->
        <!-- <van-popup v-model="show" position="top" :overlay="false">
            <van-picker :columns="columns" @change="onChange" />
        </van-popup>
        <van-popup v-model="show1" position="top" :overlay="false">
            <van-picker :columns="columns1" @change="onChange" />
        </van-popup>
        <van-popup v-model="show2" position="top" :overlay="false">
            <van-picker :columns="columns2" @change="onChange" />
        </van-popup> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            hheader: {
                title: "数据报警",
                back: "true"
            },
            count: 0,
            isLoading: false,
            list: [],
            loading: false,
            finished: false,
            dealStatus: "", //状态
            degree: "", //等级
            deviceType: "", //设备
            dealStatusTXT: "状态", //状态
            degreeTXT: "等级", //等级
            deviceTypeTXT: "设备", //设备
            pageNumber: 0, //当前页数
            totalPage: 0, //总页数
            show: false,
            show1: false,
            show2: false
        };
    },
    created() {
        this.init();
    },
    methods: {
        //修改下拉条件
        onChange(picker, value, index) {
            console.log(`当前值：${value}, 当前索引：${index}`);
        },
        //上拉刷新
        onRefresh() {
            let self = this;
            self.totalPage = 0;
            self.pageNumber = 0;
            self.init(); //加载数据
        },
        //
        onLoad() {
            let self = this;
            setTimeout(() => {
                let self = this;
             
                let data = {
                    projectId: HGetHashUrl("projectId"),
                    degree: self.degree,
                    deviceType: self.deviceType,
                    dealStatus: self.dealStatus,
                    pageNumber: self.pageNumber + 1
                };
                self.$.Get("/project/dataAlarm", data, re => {
                    self.list = self.list.concat(re.info.list);
                    self.totalPage = re.info.totalPage; //总页数
                    self.loading = false;
                    self.pageNumber ++;
                    if (self.pageNumber >= self.totalPage) {
                        self.finished = true;
                    }
                });
            }, 500);
        },
        //跳转详情页
        toDetail(v) {
            this.$router.push("/stationDetial?workId="+v.work_id);
        },
        //初始化获取
        init() {
            let self = this;
            let data = {
                projectId: HGetHashUrl("projectId"),
                degree: self.degree,
                deviceType: self.deviceType,
                dealStatus: self.dealStatus,
                pageNumber: self.pageNumber + 1
            };
            self.$.Get("/project/dataAlarm", data, re => {
                console.log(re);
                self.list = re.info.list;
                self.totalPage = re.info.totalPage;
                self.isLoading = false; //关闭下拉刷新效果
            });
        },
        dj(v) {
            v = v || "";
            this.degree = v;
            this.show = false;
            this.init();
        },
        sb(v) {
            v = v || "";
            this.deviceType = v;
            this.show1 = false;
            this.init();
        },
        zt(v) {
            v = v || "";
            this.dealStatus = v;
            this.show2 = false;
            this.init();
        }
    },
    filters: {
        degree(v) {
            switch (v) {
                case "1":
                    return "一级";
                    break;
                case "2":
                    return "二级";
                    break;
                case "3":
                    return "三级";
                    break;
                default:
                    return "  ";
                    break;
            }
        }
    }
};
</script>
<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.waring {
    .van-popup {
        top: rem(232);
    }
    .hdefalut{
        height: rem(600);
        line-height: rem(600);
        text-align: center;
    }
    .selectList {
        font-size: rem(45);
        position: relative;
        height: rem(120);
        > div {
            float: left;
            width: 33%;
            text-align: center;
            height: rem(120);
            line-height: rem(120);
            background-color: white;
            border-bottom: 1px solid #ebebeb;
        }
        .selectul {
            background-color: white;
            position: absolute;
            top: rem(120);
            left: 0;
            width: 100%;
            z-index: 100;
            li {
                height: rem(120);
                line-height: rem(120);
                text-align: center;
                border-bottom: 1px solid #ebebeb;
            }
        }
        select {
            float: left;
            width: 33%;
            height: rem(120);
            line-height: rem(120);
            border: 0;
            outline: none;
            text-align: right;
            text-align-last: center;
            appearance: none;
            option {
                width: 100%;
                text-align: right;
                text-align-last: center;
            }
        }
    }
    .selectList.liston {
        height: rem(600);
    }
}
.waring-item {
    margin: rem(20);
    border-radius: rem(10);
    background-color: white;
    font-size: rem(40);
    padding: rem(20);
    li {
        position: relative;
        height: rem(108);
        line-height: rem(108);
        border-top: 1px solid $bordercolor;
        .iconfont {
            margin-right: rem(10);
        }
        .badge {
            background-color: #f85252;
            color: white;
            padding: rem(10) rem(40);
            border-radius: rem(30);
            position: absolute;
            top: rem(10);
            right: rem(20);
            line-height: 1;
        }
        .stateName {
            color: #28c189;
            position: absolute;
            top: rem(40);
            right: rem(20);
            line-height: 1;
        }
        .stateName.error {
            color: red;
        }
    }
    li:nth-child(1) {
        border-top: 0px solid $bordercolor;
        b {
            font-weight: bold;
        }
    }
}
</style>



